import React from "react";
import { FilterMore } from "@cvccorp-components/core";

const _FilterMore = prop => {
    const { onApply, item, classes, onActives, height } = prop;

    return (
        <FilterMore
            onApply={onApply}
            filters={item.filters}
            item={item}
            className={classes.item}
            label={item.label}
            noModal={item.noModal}
            isMobile={item.isMobile}
            onClose={item.onClose}
            header={item.header}
            isOpen={item.isOpen}
            onActives={onActives}
            height={height}
        />
    );
};

export default _FilterMore;
